@import "variables.scss";

.about-us {
  max-width: 1920px;

  &-container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__text {
    margin-top: 35px;
    font-size: 20px;
    font-weight: 400;
  }

  &__main {
    display: flex;
    margin-top: 40px;
  }

  &__slider {
    @media (max-width: 1000px) {
      display: none;
    }
  }

  &__descriptions {
    margin-left: 55px;
    color: $color-pink;
    font-size: 26px;
    list-style: none;
  }

  &__descriptions-element {
    color: #000;

    &:before {
      content: '-';
      padding: 5px;
      font-weight: 900;
    }
  }
  
  &__memo {
    margin-top: 15px;
    font-size: 20px;
    color: #f15b5b;
    padding: 30px 230px;
    text-align: center;
    box-shadow: 0px 0px 2px #242424;
  }
}

.about-us__item {
  height: 400px;
  width: 600px;
}